.dropdown .dropdown-menu{-webkit-transition:all 0.5s;-webkit-transition:all 0.5s ease-in-out;transition:all 0.5s ease-in-out;max-height:0;display:block;overflow:hidden;opacity:0}.dropdown.show .dropdown-menu{max-height:300px;opacity:1}body,html{width:100%;height:100%}.btn-group-xs>.btn,.btn-xs{padding:0.35rem 0.4rem;font-size:0.875rem;line-height:0.5;border-radius:0}.full-modal{width:100vw !important;max-width:none;max-width:initial}.mesa{font-size:2em;border-radius:5px}.mesa.btn-outline-danger:focus{background:#d9534f;color:white}.mesa.btn-outline-success:focus{background:#5cb85c;color:white}@media screen and (max-width: 992px){#totalpedidos{position:fixed;left:0px;bottom:0px;width:100vw}}

